#overlay {
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: #add8e6;
  border: 1px solid #add8e6;
  padding: 50px;
  position: fixed;
  top: 0;
  left: 0;
}

#drop-zone {
  width: 80%;
  height: 50%;
  text-align: center;
  border: 5px dashed #fff;
  border-radius: 10px;
  padding: 15px;
  position: relative;
}

main {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

main > .images {
  height: 200px;
  width: 200px;
  background-position: center;
  background-size: cover;
  border: 2px dashed #f08080;
  border-radius: 10px;
  flex-shrink: 0;
}

main > .images:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.download {
  margin-top: 170px;
  margin-left: 170px;
}

.download:hover {
  transform: scale(1.2);
}

.loader {
  width: 100%;
  height: 100%;
  border: 1px solid #f08080;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader .lds-facebook {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.loader .lds-facebook div {
  width: 16px;
  background: #adff2f;
  animation: 1.2s cubic-bezier(0, .5, .5, 1) infinite lds-facebook;
  display: inline-block;
  position: absolute;
  left: 8px;
}

.loader .lds-facebook div:first-child {
  animation-delay: -.24s;
  left: 8px;
}

.loader .lds-facebook div:nth-child(2) {
  animation-delay: -.12s;
  left: 32px;
}

.loader .lds-facebook div:nth-child(3) {
  animation-delay: 0s;
  left: 56px;
}

@keyframes lds-facebook {
  0% {
    height: 64px;
    top: 8px;
  }

  50%, 100% {
    height: 32px;
    top: 24px;
  }
}

